export function writeWeatherScreen(wc_code, cloud_ndx, sky_ndx){
  let sun_pos_top = 70 - (sky_ndx*32)
  let moon_pos_top = 20
  return(<>
    {sky_ndx < 0 ? writeStars() : null }
    {sky_ndx < 0 ?
      (<img src={sky_ndx === -1 ? moon_bkgnd2 : moon_bkgnd} style={{position:"absolute", top:moon_pos_top, left:"100px", width:"50px"}} alt=""/>)
    : (<img src={sun_bkgnd} style={{position:"absolute", top:sun_pos_top}} alt=""/>)
    }
    {writeClouds(cloud_ndx)}
    {writeRain(wc_code)}
    {writeSnow(wc_code)}
    {writeFog(wc_code)}
    {writeDrizzle(wc_code)}
    {writeStorm(wc_code)}
  </>)
}

export function getCloudDesc(cloud_ndx){
  if(cloud_ndx === 0) return "No Clouds"
  if(cloud_ndx === 1) return "Slight Clouds"
  if(cloud_ndx === 2) return "Slight / Moderate Clouds"
  if(cloud_ndx === 3) return "Moderate Clouds"
  if(cloud_ndx === 4) return "Moderate / Heavy Clouds"
  if(cloud_ndx === 5) return "Heavy Clouds"
}

