export function makeCloudCount(wc_arr){

  let cur_cloud_cnt = 0

  const arr = []

  //analyze weather codes, create cloudCount array
  for(let i=0; i < wc_arr.length; i++){ //weathercode array, we are passing this array as arg cuz this function will be imported eventually
 
    let x = wc_arr[i]

    if(x === 0){ //clear sky
      cur_cloud_cnt = 0
    }
    else if(x === 1){ //clouds dissloving
      if(cur_cloud_cnt === 0)
        wc_arr[i] = 0
      cur_cloud_cnt -=2
      if(cur_cloud_cnt < 0)
        cur_cloud_cnt = 0
    }
    else if(x === 2){ //unchanged
      if(cur_cloud_cnt === 0)
        wc_arr[i] = 0
    }
    else if(x === 3){ //clouds forming
      if(cur_cloud_cnt < 4.5) cur_cloud_cnt += 0.3
    }
    else if(x === 95){ //slight thunderstorms
      if(cur_cloud_cnt < 1) cur_cloud_cnt += 1.0
      else if(cur_cloud_cnt < 4.5) cur_cloud_cnt += 0.3
    }

    arr[i] = Math.ceil(cur_cloud_cnt)
  }
  return arr
}

export function makeSkyIndex(data){

  const sky_index = []

  sky_index[0] = doSkyIndex_Day(data, 0)
  sky_index[1] = doSkyIndex_Day(data, 1)
  sky_index[2] = doSkyIndex_Day(data, 2)
  sky_index[3] = doSkyIndex_Day(data, 3)
  sky_index[4] = doSkyIndex_Day(data, 4)
  sky_index[5] = doSkyIndex_Day(data, 5)
  sky_index[6] = doSkyIndex_Day(data, 6)
  return sky_index
}

function doSkyIndex_Day( data, day_num ){

  let sunrise_stamp = data.daily.sunrise[day_num]
  let sunset_stamp = data.daily.sunset[day_num]
  let sunrise_hour = Number(sunrise_stamp.slice(11, 13))
  let sunset_hour = Number(sunset_stamp.slice(11, 13))
  if(sunrise_hour === -4) sunrise_hour = 0
  if(sunset_hour === -4) sunset_hour = 24
  let day_hours = sunset_hour - sunrise_hour - 1
  let do_repeat = !(day_hours % 2) // is even
  let day_index_max = Math.ceil(day_hours/2)

  const arr = [24]
  let asc = true
  let has_repeated = !do_repeat
  let sun_index = -sunrise_hour
  for(let i=0; i < 24; i++){
    if(asc) arr[i] = sun_index++
    else {
      if(has_repeated)
        arr[i] = sun_index--
      else {
        arr[i] = sun_index
        has_repeated = true
      }
    }
    if(sun_index === day_index_max)
      asc = false
  }
  return arr;
}

function writeStars(){
  let rnd = 0 | Math.random()*4.0
  switch(rnd){
    case 0 : return(<img className="img-h-flip" src={stars_bkgnd} style={{position:"absolute", top:"0"}} alt=""/>)
    case 1 : return(<img src={stars_bkgnd} style={{position:"absolute", top:"0"}} alt=""/>)
    case 2 : return(<img className="img-v-flip" src={stars_bkgnd} style={{position:"absolute", top:"0"}} alt=""/>)
    case 3 : return(<img className="img-hv-flip" src={stars_bkgnd} style={{position:"absolute", top:"0"}} alt=""/>)
    default: return null
  }
}


export function getSkyColor(sky_ndx){
  let bk_color
  if(sky_ndx === 0){
    bk_color = "linear-gradient(var(--sky-red), var(--sky-orange))"
  }
  else if(sky_ndx > 0){
    bk_color = "linear-gradient(var(--sky-blue2), var(--sky-blue))"
  }
  else if(sky_ndx === -1){
    bk_color = "linear-gradient(var(--sky-dkpurple2), var(--sky-dkpurple))" 
  }
  else if(sky_ndx < -1){
    bk_color = "linear-gradient(black, var(--sky-dkpurple2))"
  }
  return bk_color
}


