import AnimBkgndCanvas from './AnimBkgndCanvas'

import sunbk from '../img/sun2-bk.png'

const AppHeader = () => {
  return (
    < header id="app-header" className="app-header" data-desc="is-sizer" style={{ position: "relative", overflowY: "hidden" }}>
      <div className="flex-col" style={{ position: "relative" }}>
        <a href="https://www.kevmetz.com" style={{ textDecoration: "none" }}>
          <span style={{ zIndex: "80", position: "relative" }} className="titanium kevmetz">kevmetz.com</span>
        </a>
        {<img className="logo-header" style={{ zIndex: "50" }} src={sunbk} alt="" />}
      </div>
      {/*className="color-rotation"*/  /*rgb(0, 255, 200)*/}
      <div id="anim-bkgnd6" data-desc="is-canvas" className="color-rotation" style={{ position: "absolute", margin: "0" }}>
        <AnimBkgndCanvas sizer="app-header" anim={true} count="128" mRate="0.5" stroke="rgb(0, 255, 200)" fill="#EEEEEE11" thick="1" key="header-anim" />
      </div>
    </header >
  )
}

export default AppHeader