import React from 'react'

const BadgeMode = ({ txt, active, clk, width }) => {

  return (<>
    {active ?
      (<div onClick={clk} className="badge-mode badge-active" style={{ width: width }}>{txt}</div>)
      : (<div onClick={clk} className="badge-mode" style={{ width: width }}>{txt}</div>)
    }
  </>)
}

export default BadgeMode