
//"https://api.open-meteo.com/v1/forecast?latitude=52.52&longitude=13.41&hourly=temperature_2m,apparent_temperature,precipitation,weathercode,windspeed_10m&daily=weathercode,temperature_2m_max,temperature_2m_min,apparent_temperature_max,apparent_temperature_min,precipitation_sum&current_weather=true&temperature_unit=celsius&windspeed_unit=kmh&precipitation_unit=mm&timeformat=unixtime",

import axios from "axios";

import {useEffect, useState, useContext, useCallback} from "react"
import {Loader} from '@googlemaps/js-api-loader';

import {writeWeatherScreen, makeWeatherCodeDesc, makeSkyIndex, getSkyColor, makeCloudCount, getCloudDesc} from './makeWeather'

import { ModalContext } from "../Layout"
import BasicButton from "../comp/BasicButton"
import BadgeMode from "../comp/BadgeMode";

import red_thermo from '../img/thermo-icons/thermo-red.png'      
import org_thermo from '../img/thermo-icons/thermo-orange.png'      
import ylw_thermo from '../img/thermo-icons/thermo-yellow.png'      
import grn_thermo from '../img/thermo-icons/thermo-green.png'      
import blu_thermo from '../img/thermo-icons/thermo-blue.png'      
import pur_thermo from '../img/thermo-icons/thermo-purple.png'      
import emp_thermo from '../img/thermo-icons/empty-thermo.png'      
import wind1 from '../img/thermo-icons/wind2-sm.png'      

import satIconImg from '../img/s34.gif'
import satPulseImg from '../img/pulse4.gif'

// attach initMap to window object to make it global
window.initMap = async function () {

  const myContent = document.createElement("div");
  myContent.classList.add("satContainer");

  const satIcon = document.createElement("img");
  satIcon.src = satIconImg
  satIcon.classList.add("satIcon");
  myContent.appendChild(satIcon)

  const satPulse = document.createElement("img");
  satPulse.src = satPulseImg
  satPulse.classList.add("satPulse");
  myContent.appendChild(satPulse)

  const satTarget = document.createElement("div");
  satTarget.classList.add("satTarget");
  myContent.appendChild(satTarget)

  let map = new window.google.maps.Map(document.getElementById("map"), { //creates a map node and appends it to map element
    center: {lat: window.weather.latitude, lng: window.weather.longitude},
    zoom: 4,
    draggable: true,
    mapId: process.env.REACT_APP_LMN
  });

  const mrk = new window.google.maps.marker.AdvancedMarkerElement({
    map,
    position: {lat: window.weather.latitude, lng: window.weather.longitude},
    title: window.weather.latitude  + ", " + window.weather.longitude,
    gmpDraggable: true,
    content: myContent,
  });

  document.getElementById("lat-input").value = window.weather.latitude
  document.getElementById("lng-input").value = window.weather.longitude  

  window.google.maps.event.addListener(map, "click", ({ latLng }) => {
    let _lat = latLng.lat()
    let _lng = latLng.lng()
    mrk.position = latLng
    mrk.title = _lat  + ", " + _lng
    window.weather.latitude = _lat
    window.weather.longitude = _lng
    document.getElementById("lat-input").value = _lat
    document.getElementById("lng-input").value = _lng
  })

  mrk.addListener('dragend', function (event) {
    //position has already been updated by drag
    let _lat = event.latLng.lat()
    let _lng = event.latLng.lng()
    mrk.title = String(_lat + ", " + _lng)
    window.weather.latitude = _lat
    window.weather.longitude = _lng
    document.getElementById("lat-input").value = _lat
    document.getElementById("lng-input").value = _lng
  })

  document.getElementById("go-btn").addEventListener("click", (event) => {
    let _lat = Number(document.getElementById("lat-input").value)
    let _lng = Number(document.getElementById("lng-input").value)
    mrk.position = { lat: _lat, lng: _lng }
    mrk.title = String(_lat + ", " + _lng)
    window.weather.latitude = _lat
    window.weather.longitude = _lng
    map.setCenter({ lat: _lat, lng: _lng })
    window.scrollTo(0, document.getElementById("map").getBoundingClientRect().top)
  })
}//initMap

//--

const WeatherPage5 = () => {

  const { showModal, hideModal, checkModal } = useContext(ModalContext);

  const[weatherData, setWeatherData] = useState(null)
  const[spliceStart, setSpliceStart] = useState(0)
  const[spliceEnd, setSpliceEnd] = useState(24)
  const[selectedDayNum, setSelectedDayNum] = useState(1)//which day to display from 1 - 7
  const[displayMode, setDisplayMode] = useState(0) //display 24-hour, first-8, middle-8, last-8
  const[cloudCountArr, setCloudCountArr] = useState(null)
  const[skyIndex, setSkyIndex] = useState([])
  const[showInfo, setShowInfo] = useState(true)

  const fetchWeatherData = useCallback((modal=false, checkin=false)=>{
    if(modal) showModal(1)
    axios.get("https://api.open-meteo.com/v1/forecast?current_weather=true&daily=sunrise,sunset&hourly=temperature_2m,relativehumidity_2m,windspeed_10m,weathercode",
      { params: {
          latitude: window.weather.latitude,
          longitude: window.weather.longitude,
          timezone: "auto"
        },
      }
    )
    .then(({data}) => {
      if(data) {
        setWeatherData(data)
        setSkyIndex(makeSkyIndex(data))
        setCloudCountArr(makeCloudCount(data.hourly.weathercode))
      } 
      if(modal) hideModal()      
      if(checkin) checkModal()      
    })
    .catch(()=>{
      alert("No data available")
      if(modal) hideModal()
      if(checkin) checkModal()            
    })
  }, [hideModal, checkModal, showModal])

  useEffect(()=>{ //map init - use env variable, research libraries array

    //window.scrollTo(0,0)

    if(!window.weather){
      window.weather = {
        latitude: 43.471468,
        longitude: -80.544205
      }
    }

    const loader = new Loader({ // use env variable
      apiKey: process.env.REACT_APP_XYZ,
      version: "weekly",
      libraries: ["maps", "marker"]
    });
  
    loader.load()
    .then( async() => { 
      await window.initMap();
      checkModal()
    })
    .catch(e => { alert("Error: google maps failed to load") });  

    fetchWeatherData(false, true)

  }, [checkModal, fetchWeatherData])//


  return (
    <section className="page-section">

      <article className="parent-article flex-col trans-zero">
        <div id="map" style={{width:"100%", height:"400px", zIndex:"80"}} data-desc="is container"></div>
      </article>

      <article className="parent-article flex- trans-zero">
        <div className="flex-row wrap row-stretch">
          
          <div className="child-article flex-col trans-blue" style={{flex:"1"}}>

            <div className="flex-row">
              <label className="text-label-blue" style={{marginBottom:"1rem"}} htmlFor="lat-input">Latitude :</label>
              <input id="lat-input"  style={{marginBottom:"1rem"}} onChange={e=>handleLatitude(e)} className="text-input-blue w-30" type="text"></input>
            </div>
            <div className="flex-row">
              <label className="text-label-blue" style={{marginBottom:"1rem"}} htmlFor="lng-input">Longitude :</label>
              <input id="lng-input"  style={{marginBottom:"1rem"}} onChange={e=>handleLongitude(e)} className="text-input-blue w-30" type="text"></input>
            </div>    
            <div className="flex-row">
              <button onClick={()=>h65()}className="button-blue" tabIndex="0" style={{marginRight:"1rem"}}>Device Location</button> 
              <button id="go-btn" className="button-blue" tabIndex="0">Set Location</button> 
            </div>    
          </div>

          <div className="child-article flex-row wrap trans-blue" style={{flex:"1", color:"var(--text-white"}}>


              <BasicButton txt="Get Weather" fn={()=>h57()}/>
            
            {weatherData ? (
              <article className="parent-article flex-col col-left">
                <div>latitude : {weatherData.latitude}</div>
                <div>longitude : {weatherData.longitude}</div>
                <div>time zone : {weatherData.timezone}</div>
                <div>GMT time offset (hrs) : {weatherData.utc_offset_seconds/3600}</div>
              </article>
            ) : null}
          </div>

        </div>    
      </article>

      {weatherData ? (
        <>
          <article id="days" className="parent-article flex-col trans-zero">
          <div className="flex-row row-space" style={{width:"100%", maxWidth:"760px"}}>
            <BadgeMode clk={()=>h55(1)} txt="Day 1" active={selectedDayNum === 1} width="5rem"/>
            <BadgeMode clk={()=>h55(2)} txt="Day 2" active={selectedDayNum === 2} width="5rem"/>
            <BadgeMode clk={()=>h55(3)} txt="Day 3" active={selectedDayNum === 3} width="5rem"/>
            <BadgeMode clk={()=>h55(4)} txt="Day 4" active={selectedDayNum === 4} width="5rem"/>
            <BadgeMode clk={()=>h55(5)} txt="Day 5" active={selectedDayNum === 5} width="5rem"/>
            <BadgeMode clk={()=>h55(6)} txt="Day 6" active={selectedDayNum === 6} width="5rem"/>
            <BadgeMode clk={()=>h55(7)} txt="Day 7" active={selectedDayNum === 7} width="5rem"/>
          </div>
          <div className="child-article flex-col day-detail" style={{alignSelf:"stretch"}}>
            {writeSelectedDayDesc()}
          </div>
          <div className="flex-row row-space" style={{width:"100%", maxWidth:"450px"}}>
            <BadgeMode clk={()=>h56(0)} txt="12am - 11pm" active={displayMode === 0} width="4.7rem"/>
            <BadgeMode clk={()=>h56(1)} txt="12am - 7am"  active={displayMode === 1} width="4.7rem"/>
            <BadgeMode clk={()=>h56(2)} txt=" 8am - 3pm " active={displayMode === 2} width="4.7rem"/>
            <BadgeMode clk={()=>h56(3)} txt=" 4pm - 11pm" active={displayMode === 3} width="4.7rem"/>
          </div>
        </article>

        <article className="parent-article flex-row wrap trans-zero">

          {weatherData.hourly.time.slice(spliceStart, spliceEnd).map( (_x, ndx) => { /* MAP */
            
            let this_ndx = spliceStart + ndx
            let hour_wc = weatherData.hourly.weathercode[this_ndx]
            let sky_ndx;
            if(displayMode === 0 || displayMode === 1) sky_ndx = skyIndex[selectedDayNum-1][ndx]
            else if(displayMode === 2) sky_ndx = skyIndex[selectedDayNum-1][ndx+8]
            else if(displayMode === 3) sky_ndx = skyIndex[selectedDayNum-1][ndx+16]

            return (
              <article className="flex-col" key={this_ndx}>          
                 {writeWeatherCodeSection(hour_wc, sky_ndx, this_ndx, _x)} 
              </article>
            )//
          })/*end map*/}

        </article>
        </>
      ) : (<p>no weather data</p>)
      /*end if weather data*/}
    </section>
  )//return

  //handlers

  function writeSelectedDayDesc(){

    let sunset = weatherData.daily.sunset[selectedDayNum-1]
    let sunrise = weatherData.daily.sunrise[selectedDayNum-1]
    let date_txt = weatherData.daily.time[selectedDayNum-1]
    let sunrise_txt = sunrise.slice(11,16)
    if(sunrise_txt === "-4:00")
      sunrise_txt = "midnight"
      let sunset_txt = sunset.slice(11,16)
      if(sunset_txt === "-4:00")
        sunset_txt = "midnight"
  
    let dte = new Date(date_txt)
    let day_txt = getDayName(dte.getUTCDay())

    return(
      <>
      <div>{date_txt}</div>
      <div style={{color:"var(--text-yellow", fontSize:"1.3rem"}}>{day_txt}</div>
      <div>{getDayDesc(selectedDayNum)}</div>      
      <div>Sunrise at {sunrise_txt}</div>
      <div>Sunset at {sunset_txt}</div>
      </>
    )
  }

  function h65(){
    showModal(1)
    
    navigator.geolocation.getCurrentPosition(positionSuccess, positionFailure);

    function positionSuccess({ coords }) {
        //console.log(coords)
        document.getElementById("lat-input").value = coords.latitude
        document.getElementById("lng-input").value = coords.longitude  
        window.weather.latitude = coords.latitude
        window.weather.longitude = coords.longitude             
        hideModal()
        document.getElementById("go-btn").click()
        //Intl.DateTimeFormat().resolvedOptions().timeZone
    }
    function positionFailure() {
      console.log("Error: failed to get geolocation");
      hideModal()
    }    
  }

  function h57(){ //handle get weather
    fetchWeatherData(true, false)
  }

  function h56(display_mode){ //handle set display mode
    setDisplayMode(display_mode)
    updateSplice(selectedDayNum, display_mode)
  }

  function h55(day_num){ //handle day select
    setSelectedDayNum(day_num)
    updateSplice(day_num, displayMode)
  }

  function updateSplice(day_num, display_mode){
    if(day_num === 1){
      if(display_mode === 0) setSplice(0, 24)
      else if (display_mode === 1) setSplice(0, 8)
      else if (display_mode === 2) setSplice(8, 16)
      else if (display_mode === 3) setSplice(16, 24)
    }
    else if(day_num === 2){
      if(display_mode === 0) setSplice(24+0, 24+24)
      else if (display_mode === 1) setSplice(24+0, 24+8)
      else if (display_mode === 2) setSplice(24+8, 24+16)
      else if (display_mode === 3) setSplice(24+16, 24+24)
    }
    else if(day_num === 3){
      if(display_mode === 0) setSplice(48+0, 48+24)
      else if (display_mode === 1) setSplice(48+0, 48+8)
      else if (display_mode === 2) setSplice(48+8, 48+16)
      else if (display_mode === 3) setSplice(48+16, 48+24)
    }
    else if(day_num === 4){
      if(display_mode === 0) setSplice(72+0, 72+24)
      else if (display_mode === 1) setSplice(72+0, 72+8)
      else if (display_mode === 2) setSplice(72+8, 72+16)
      else if (display_mode === 3) setSplice(72+16, 72+24)
    }
    else if(day_num === 5){
      if(display_mode === 0) setSplice(96+0, 96+24)
      else if (display_mode === 1) setSplice(96+0, 96+8)
      else if (display_mode === 2) setSplice(96+8, 96+16)
      else if (display_mode === 3) setSplice(96+16, 96+24)
    }
    else if(day_num === 6){
      if(display_mode === 0) setSplice(120+0, 120+24)
      else if (display_mode === 1) setSplice(120+0, 120+8)
      else if (display_mode === 2) setSplice(120+8, 120+16)
      else if (display_mode === 3) setSplice(120+16, 120+24)
    }
    else if(day_num === 7){
      if(display_mode === 0) setSplice(144+0, 144+24)
      else if (display_mode === 1) setSplice(144+0, 144+8)
      else if (display_mode === 2) setSplice(144+8, 144+16)
      else if (display_mode === 3) setSplice(144+16, 144+24)
    }
  }

  function setSplice(start, end){
    setSpliceStart(start)
    setSpliceEnd(end)
  }
  
  function writeWeatherCodeSection(wc_code, sky_ndx, this_ndx, _x){    
    
    let wco = makeWeatherCodeDesc(wc_code)
    let bk_color = getSkyColor(sky_ndx)
    let cloud_ndx = cloudCountArr[this_ndx]
    let cur_temp = weatherData.hourly.temperature_2m[this_ndx]
    let round_temp = 0 | cur_temp
    let temp_fahr = Math.roundF((cur_temp*9.0/5.0) + 32.0, 1)
    let temp_ndx = getTempIndex(cur_temp)
    let cur_wind = weatherData.hourly.windspeed_10m[this_ndx]
    let wind_mph = Math.roundF(cur_wind/1.6, 1)
    let round_wind = 0 | cur_wind
    let wind_text = String(round_wind)
    if(wind_text.length < 3) wind_text += " "
    wind_text += "km/h"
    let time_text = _x.slice(11,16)
    let time_hours = Number(_x.slice(11,13))
    let ampm_hours = Math.toAMPM(time_hours)
    let ampm_text = String(ampm_hours) + (time_hours > 11 ? " pm" : " am")

    return (<>
      <article className="child-article flex-col col-center" style={{background:bk_color, position:"relative", overflow:"hidden", height:"200px", maxWidth:"300px", minWidth:"300px", padding:"0"}}>
        
        {writeWeatherScreen(wc_code, cloud_ndx, sky_ndx)}
        {writeThermoSection(temp_ndx, round_temp, wind_text, ampm_text)}        
        <article className="child-article flex-col weather-detail" style={ showInfo ? {visibility:"visible"} : {visibility:"hidden"}}>
        
          <div>Time: {time_text}&nbsp;&nbsp;({ampm_text})</div>
          <div style={{color:"var(--text-yellow)"}}>{wco.shortdesc}</div>
          <div style={{color:"var(--text-yellow)"}}>{getCloudDesc(cloud_ndx)}</div>
          <div>Temp: {cur_temp} &deg;C&nbsp;&nbsp;({temp_fahr} &deg;F)</div>
          <div>Humidity: {weatherData.hourly.relativehumidity_2m[this_ndx]} %</div>
          <div>Wind: {cur_wind} km/h&nbsp;({wind_mph} mph)</div>
          <div>Weather Code: {wc_code} </div>
          {/*<div>Sky Index: {sky_ndx}</div>*/}

          <div onClick={(e)=>handle66(e)} style={{position:"absolute", width:"100%", height:"100%", zIndex:"80"}}></div>
        </article> 
      </article>
    </>)        
  }//

  function handle66(e){
    e.target.parentElement.setAttribute('style', "visibility:hidden")
  }

  function handle67(e){
    let is_vis = e.target.parentElement.parentElement.lastChild.getAttribute('style', "visibility")
    if(is_vis === "visibility:hidden"){
      e.target.parentElement.parentElement.lastChild.setAttribute('style', "visibility:visible")
    }
    else{
      showInfo ? setShowInfo(false) : setShowInfo(true)
    }
  }

  function writeThermoSection(temp_ndx, round_temp, wind_text, time_text){
    return(
    <div className="thermo-section">
      <span style={{marginBottom:"6px"}}>{time_text}</span>      
      {writeThermoImg(temp_ndx)}
      <span>{round_temp}&deg;C</span>
      <img src={wind1} style={{width:"40px", paddingTop:"4px"}} alt="" />
      <span>{wind_text}</span>
      
      <div onClick={(e)=>handle67(e)} style={{position:"absolute", width:"60px", height:"100%", zIndex:"80", cursor:"pointer"}}></div>      
  
    </div>
    )
  }

  function writeThermoImg(temp_ndx){
    return(<>
      {temp_ndx === 6 ? <img src={red_thermo} style={{width:"20px"}} alt="" /> : null}
      {temp_ndx === 5 ? <img src={org_thermo} style={{width:"20px"}} alt="" /> : null}
      {temp_ndx === 4 ? <img src={ylw_thermo} style={{width:"20px"}} alt="" /> : null}
      {temp_ndx === 3 ? <img src={grn_thermo} style={{width:"20px"}} alt="" /> : null}
      {temp_ndx === 2 ? <img src={blu_thermo} style={{width:"20px"}} alt="" /> : null}
      {temp_ndx === 1 ? <img src={pur_thermo} style={{width:"20px"}} alt="" /> : null}
      {temp_ndx === 0 ? <img src={emp_thermo} style={{width:"20px"}} alt="" /> : null}    
    </>)
  }
  
  function getTempIndex(cur_temp){
    if(cur_temp >= 30.0) return 6
    if(cur_temp >= 21.0) return 5
    if(cur_temp >= 12.0) return 4
    if(cur_temp >= 3.0) return 3
    if(cur_temp >= -6.0) return 2
    if(cur_temp >= -15.0) return 1
    return 0
  }

  function handleLatitude(e){

    if(isNaN(e.target.value)) return
    let newVal = e.target.value
    if(newVal < -180.0) return
    if(newVal > 180.0) return
    window.weather.latitude = newVal
  }
  
  function handleLongitude(e){
    if(isNaN(e.target.value)) return
    let newVal = e.target.value
    if(newVal < -180.0) return
    if(newVal > 180.0) return
    window.weather.longitude = newVal
  }


  function getDayName(day_num){
    if(day_num === 0) return "Sunday"
    if(day_num === 1) return "Monday"
    if(day_num === 2) return "Tuesday"
    if(day_num === 3) return "Wednesday"
    if(day_num === 4) return "Thursday"
    if(day_num === 5) return "Friday"
    if(day_num === 6) return "Saturday"
  }

  function getDayDesc(day_num){
    if(day_num === 1) return "Day 1 - Today"
    if(day_num === 2) return "Day 2 - Tomorrow"
    if(day_num === 3) return "Day 3"
    if(day_num === 4) return "Day 4"
    if(day_num === 5) return "Day 5"
    if(day_num === 6) return "Day 6"
    if(day_num === 7) return "Day 7"
  }

}

export default WeatherPage5