import {HashRouter, Routes, Route} from 'react-router-dom'

import Layout from './Layout'
import WeatherPage from './pg/WeatherPage'


function App() {
 
  return (
    <HashRouter>
      <Layout>
        <Routes>
          <Route>
            <Route path="/" element={<WeatherPage />}></Route>
            <Route path="weather" element={<WeatherPage />}></Route>
          </Route>
        </Routes>
      </Layout>    
    </HashRouter>
  )
}

export default App

