import React, {useState, useEffect, useRef} from 'react'
import {makeShapes1, moveShapes1, drawHexagon} from './anim-bkgnd-lib'

const AnimBkgndCanvas = ({sizer, anim, stroke="#444444FF", thick="5", fill="#d5d5d566", bkgnd="#FFFFFF00", count="10", mRate="1.0"}) => {

  const [drawWidth, setDrawWidth] = useState(-1)
  const [drawHeight, setDrawHeight] = useState(-1)
  const [shapes, setShapes] = useState([])
  const [initIsDone, setInitIsDone] = useState(false)

  const timerRef = useRef(null);
  const canvRef = useRef(null);
  
  useEffect(() => {
    function resizeCanvas(){
       let x = document.getElementById(sizer)
       let r = x.getBoundingClientRect()
       setDrawWidth(r.width);
       setDrawHeight(r.height);  
    }    
    resizeCanvas() 
    window.addEventListener("resize", resizeCanvas)

    //observer
    const targetNode = document.getElementById("app-main");
    const config = { attributes: true, childList: true, subtree: true, attributeFilter: ["height"]};
    const callback = (mutationList, observer) => {
        resizeCanvas();
    };
    const observer = new MutationObserver(callback);
    observer.observe(targetNode, config);
    
    return ()=>{
      window.removeEventListener("resize", resizeCanvas)
      observer.disconnect();      
    }
  }, [drawWidth, drawHeight, sizer]);

  useEffect(() => {  

    if(!initIsDone){
      let arr = []
      let x = document.getElementById(sizer)
      let r = x.getBoundingClientRect()
      makeShapes1(arr, r.width, r.height, count, -15, 1, mRate)
      setShapes([...arr])
      setInitIsDone(true)
    }  
    runShapes()
    
    //--
    function runShapes() {
      clearTimeout(timerRef.current);
      if(anim) moveShapes1(shapes, drawWidth, drawHeight, -15, 1)                 
      drawCanvas()      
      if(anim) timerRef.current = setTimeout(()=>runShapes(), 33);
    }
    //--
    function drawCanvas(){
      let ctx = canvRef.current.getContext('2d')  
      ctx.clearRect(0, 0, drawWidth, drawHeight);
      ctx.fillStyle=bkgnd
      ctx.fillRect(0, 0, drawWidth, drawHeight); 
      ctx.fillStyle=fill
      ctx.strokeStyle=stroke
      ctx.lineWidth=thick
      for(const c of shapes){
        if(c.r > 0)
          drawHexagon(ctx, c.x, c.y, c.r, false);
      }
    }
    //--
    return ()=>{ clearTimeout(timerRef.current) }
  }, [drawWidth, drawHeight, anim, sizer, count, mRate, shapes, bkgnd, fill, stroke, thick, initIsDone])
  
 
  /*cant use 100% on the canvas tag it only accepts pixels*/
  /*when no width or height default is 300 x 150*/
  return (   
      <canvas ref={canvRef} width={drawWidth} height={drawHeight} onContextMenu={(e)=>{e.preventDefault()}} style={{display:"flex"}}/>    
  )
}

export default AnimBkgndCanvas